import slide from 'components/slide/index'
import rankList from 'components/rankList/index'
import http from 'js/http'

export default {
  name: "index",
  components: {
    slide,
    rankList
  },
  data() {
    return {
      dataList: [1, 2, 3, 4, 5],
      nodeList: [],
      nodeTab: [],
      currentNodeId: '',
      page: 1,
      counttime: "",
      loadShow: true
    };
  },
  deactivated() {

  },
  methods: {
    selectTab(item) {
      if (item.id === this.currentNodeId) return
      this.currentNodeId = item.id
      sessionStorage.setItem("nodeRankType", item.id);
      this.page = 1
      this.nodeList = []
      this.loadShow = true
      this.$refs.vueLoad.onBottomLoaded();
      this.getNodeList()
    },
    handleBottom() {
      this.page++
      this.getNodeList()
    },
    getNodeTab() {
      http.post('/node', {}, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.nodeTab = res.content
        this.currentNodeId = sessionStorage.getItem('nodeRankType') || res.content[0].id
        this.getNodeList()
      })
    },
    getNodeList() {
      http.post('/node/vote', {
        id: this.currentNodeId,
        page: this.page,
        page_size: 10
      }, (res) => {
        if (this.loadShow) this.loadShow = false
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.nodeList = this.nodeList.concat(res.content.list)
        this.counttime = res.content.counttime
        if (this.nodeList.length < parseInt(res.content.count)) {
          this.$refs.vueLoad.onBottomLoaded();
        } else {
          this.$refs.vueLoad.onBottomLoaded(false);
        }
      })
    },
  },
  created() {
    this.getNodeTab()
  },
  mounted() {
  }
}



